@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../resources/fonts/Roboto.woff") format("woff"), url("../resources/fonts/Roboto.woff2") format("woff2"), url("../resources/fonts/Roboto.ttf") format("ttf"), url("../resources/fonts/Roboto.svg") format("svg");
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Onest", sans-serif;
  background: #F8F8FA;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

input:focus-visible, textarea:focus-visible {
  outline: none;
  border: 1px solid var(--main-blue);
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.container {
  max-width: var(--container-width);
  padding: 0 var(--container-offset);
  margin: 0 auto;
}
@media (max-width: 767.98px) {
  .container {
    max-width: 540px;
  }
}
@media (max-width: 540.98px) {
  .container {
    max-width: none;
    width: 100%;
    padding: 0 20px;
  }
}

.main-title {
  font-size: 52px;
  font-weight: 600;
  line-height: 48px;
  letter-spacing: -0.02em;
  color: var(--color-font);
}
@media (max-width: 767.98px) {
  .main-title {
    font-size: 42px;
    text-align: center;
  }
}
@media (max-width: 540.98px) {
  .main-title {
    font-size: 34px;
    line-height: 38px;
    text-align: left;
  }
}

.title {
  font-size: 40px;
  font-weight: 500;
  line-height: 36px;
  color: var(--color-font);
}
@media (max-width: 767.98px) {
  .title {
    font-size: 30px;
  }
}
@media (max-width: 540.98px) {
  .title {
    font-size: 28px;
    line-height: 32px;
  }
}

:root {
  --font-family: "Roboto", sans-serif;
  --content-width: 770px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --main-blue: #2D1AA7;
  --color-font: #202326;
  --grid-gap: 30px;
}

@media (max-width: 767.98px) {
  .nav {
    position: absolute;
    top: 50px;
    opacity: 0;
    transform: translateX(-110%);
    right: 0;
    width: 100%;
    padding: 20px;
    background: #fff;
    border-radius: 10px;
    z-index: 5;
    transition: all 0.3s ease-in-out;
  }
  .nav.is-active {
    opacity: 1;
    transform: none;
  }
}
.nav-list {
  display: flex;
  align-items: center;
}
@media (max-width: 767.98px) {
  .nav-list {
    flex-direction: column;
  }
}
.nav-item:not(:last-child) {
  margin-right: 35px;
}
@media (max-width: 767.98px) {
  .nav-item {
    margin-right: 0 !important;
    padding: 15px 0;
  }
}
.nav__link {
  font-size: 15px;
  font-weight: 500;
  line-height: 15px;
  color: var(--color-font);
}

.custom-select {
  padding: 0;
  border: none;
  background: transparent;
  font-family: "Onest", sans-serif;
  font-size: 15px;
  font-style: inherit;
  font-weight: 500;
  line-height: 15px;
  color: var(--color-font);
}
.custom-select::after {
  width: 8px;
  height: 6px;
  margin-left: 5px;
  background-image: url("data:image/svg+xml,%3Csvg width='8' height='6' viewBox='0 0 8 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1.5L4 4.5L7 1.5' stroke='%23202326' stroke-width='1.5'/%3E%3C/svg%3E ");
  background-repeat: no-repeat;
}
.custom-select__dropdown {
  width: 200px;
  padding: 14px 0;
  border: none;
}
@media (max-width: 767.98px) {
  .custom-select__dropdown {
    top: 20px;
    border: 1px solid var(--color-font);
  }
}
@media (max-width: 540.98px) {
  .custom-select__dropdown {
    left: 50%;
    transform: translateX(-50%);
  }
}
.custom-select__option {
  padding-left: 15px;
}

.select {
  width: 238px;
}
@media (max-width: 767.98px) {
  .select {
    width: 100%;
    margin-top: 12px;
  }
}
.select.itc-select_show {
  border-radius: 10px;
  border: 1px solid var(--main-blue);
}
.select__toggle {
  border: none;
  border-radius: 10px;
  height: 50px;
}
.select__toggle::after {
  content: "";
  width: 9px;
  height: 6px;
  background-image: url("data:image/svg+xml,%3Csvg width='8' height='6' viewBox='0 0 8 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1.5L4 4.5L7 1.5' stroke='%23202326' stroke-width='1.5'/%3E%3C/svg%3E ");
  background-repeat: no-repeat;
}
.select__dropdown {
  max-height: none;
  top: 55px;
  border: none;
  border-radius: 6px;
}
.select__option {
  padding: 14px 0 17px 14px;
  transition: all 0.3s ease-in-out;
}
.select__option:hover {
  border-left: 7px solid var(--main-blue);
  background: #F1F1F3;
}

.btn {
  padding: 10px 20px 12px 20px;
  background: var(--main-blue);
  border-radius: 100px;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  color: #fff;
}

.dialog {
  position: absolute;
  display: none;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
}
@media (max-width: 540.98px) {
  .dialog {
    max-width: 320px;
    width: 100%;
  }
}
.dialog.is-active {
  display: block;
}
.dialog-wrapper {
  position: relative;
  max-width: 380px;
  padding: 17px 26px;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0px 18px 60px 0px rgba(0, 0, 0, 0.0509803922);
}
.dialog-close {
  position: absolute;
  top: 13px;
  right: 13px;
  width: 22px;
  height: 22px;
  background-image: url("data:image/svg+xml,%3Csvg width='22' height='22' viewBox='0 0 22 22' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M9.58579 11L4.63604 15.9498L6.05025 17.364L11 12.4142L15.9497 17.364L17.364 15.9498L12.4142 11L17.364 6.05026L15.9497 4.63604L11 9.58579L6.05025 4.63604L4.63604 6.05026L9.58579 11Z' fill='%23202326'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
  cursor: pointer;
}
.dialog-title {
  margin-bottom: 8px;
  font-size: 28px;
  font-weight: 500;
  line-height: 36px;
  letter-spacing: -0.02em;
  color: var(--color-font);
}
.dialog-subtitle {
  margin-bottom: 30px;
  font-size: 12px;
  font-weight: 400;
  line-height: 17px;
  letter-spacing: 0.01em;
  color: var(--color-font);
}
.dialog-form {
  display: flex;
  flex-direction: column;
}
.dialog__label {
  position: relative;
  margin-bottom: 12px;
}
.dialog__label span {
  position: absolute;
  top: 3px;
  left: 15px;
  font-size: 9px;
  font-weight: 500;
  line-height: 14px;
  color: #777777;
}
.dialog__input {
  width: 100%;
  height: 50px;
  padding-left: 15px;
  border: 1px solid #EDEDF1;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 400;
  line-height: 15px;
}
.dialog__btn {
  height: 50px;
  margin: 22px 0 60px;
}
.dialog__privacy {
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: 0.01em;
  text-align: center;
  color: var(--color-font);
}
.dialog__privacy a {
  color: var(--main-blue);
}

.overlay {
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  background: rgba(32, 35, 38, 0.6980392157);
  z-index: 5;
}
.overlay.is-active {
  display: block;
}

.header {
  margin-bottom: 37px;
}
.header-wrapper {
  position: relative;
  padding: 18px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 767.98px) {
  .header-btn {
    display: none;
  }
}
.header-btn_mobile {
  display: none;
}
@media (max-width: 767.98px) {
  .header-btn_mobile {
    display: block;
    margin: 0 auto;
  }
}
.header-burger {
  position: relative;
  height: 10px;
}
.header-burger__line {
  display: block;
  width: 20px;
  height: 2px;
  background: var(--color-font);
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
}
.header-burger__line::after {
  content: "";
  position: absolute;
  bottom: 0;
  display: block;
  width: 100%;
  height: 2px;
  background: var(--color-font);
  transition: transform 0.3s ease-in-out;
}
.header-burger__line.is-active {
  transform: rotate(45deg);
}
.header-burger__line.is-active::after {
  transform: rotate(-95deg);
}

.way {
  margin-top: 100px;
}
.way-title {
  margin-bottom: 37px;
}
@media (max-width: 767.98px) {
  .way-title {
    margin-bottom: 11px;
  }
}
.way-image {
  position: relative;
  padding-bottom: 80px;
  border-bottom: 2px solid #ECECF3;
}
@media (max-width: 767.98px) {
  .way-image {
    display: flex;
    flex-direction: column;
  }
}
@media (max-width: 767.98px) {
  .way-image picture {
    text-align: center;
    order: 2;
  }
}
.way-image picture img {
  width: 100%;
}
@media (max-width: 767.98px) {
  .way-image picture img {
    width: auto;
  }
}
