  position: absolute;
  top: 16px;
  right: 16px;
  width: 112px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 18px;
  background: rgba(255, 255, 255, 0.9);
  font-family: Onest;
  font-size: 24px;
  font-weight: 500;
  line-height: 25px;
  text-align: center;
}
@media (max-width: 767.98px) {
  .way-image__date {
    display: none;
  }
}
.way-image__date::after {
  content: "";
  position: absolute;
  top: 32px;
  right: 28px;
  width: 7px;
  height: 7px;
  background-image: url("data:image/svg+xml,%3Csvg width='5' height='7' viewBox='0 0 5 7' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M5 3.5L0 7V0L5 3.5Z' fill='%23202326'/%3E%3C/svg%3E ");
  background-repeat: no-repeat;
}
.way-image__descr {
  margin-top: 20px;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: var(--color-font);
}
@media (max-width: 767.98px) {
  .way-image__descr {
    margin-top: 0;
    margin-bottom: 20px;
    order: 1;
  }
}
@media (max-width: 767.98px) {
  .way-image__line {
    position: absolute;
    bottom: -2px;
    width: 70px;
    height: 2px;
    background: var(--main-blue);
  }
}
.way-steps {
  margin-top: 25px;
}
.way-steps-item {
  position: relative;
}
.way-steps-item:nth-child(2) {
  padding: 60px 0 100px;
  background: #F1F1F3;
}
@media (max-width: 540.98px) {
  .way-steps-item:nth-child(2) {
    padding: 20px 0 40px;
  }
}
.way-steps-item:nth-child(3) {
  padding: 50px 0 80px;
}
@media (max-width: 540.98px) {
  .way-steps-item:nth-child(3) {
    padding: 50px 0 30px;
  }
}
.way-steps-item__content {
  padding-bottom: 80px;
}
@media (max-width: 540.98px) {
  .way-steps-item__content {
    padding-bottom: 50px;
  }
}
.way-steps-item__title {
  margin-bottom: 30px;
  font-size: 28px;
  font-weight: 500;
  line-height: 36px;
  letter-spacing: -0.02em;
  color: var(--color-font);
}
@media (max-width: 540.98px) {
  .way-steps-item__title {
    font-size: 22px;
    line-height: 30px;
  }
}
.way-steps-item__icon {
  position: absolute;
  width: 38px;
  height: 38px;
}
@media (max-width: 540.98px) {
  .way-steps-item__icon {
    position: inherit;
    margin-bottom: 17px;
  }
}
.way-steps-item__descr {
  font-size: 22px;
  font-weight: 400;
  line-height: 31px;
  color: var(--color-font);
}
@media (max-width: 767.98px) {
  .way-steps-item__descr {
    font-size: 18px;
    line-height: 25px;
  }
}
.way-steps-item__descr_padding {
  padding-left: 58px;
}
@media (max-width: 540.98px) {
  .way-steps-item__descr_padding {
    padding: 0;
  }
}
.way-steps-item__author {
  display: block;
  padding-left: 58px;
  margin-top: 26px;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0.01em;
  color: #777777;
}
@media (max-width: 540.98px) {
  .way-steps-item__author {
    padding-left: 0;
    font-size: 15px;
  }
}
.way-steps-item__wrapper {
  padding-left: 58px;
}
@media (max-width: 540.98px) {
  .way-steps-item__wrapper {
    padding: 0;
  }
}
.way-steps-item__advertising {
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  color: var(--color-font);
}
.way-steps-item__advertising a {
  font-size: 12px;
  line-height: 18px;
  color: var(--main-blue);
  text-decoration: underline;
}
.way-steps-item_bg {
  background: #F1F1F3;
}
.way-steps-item_bg img {
  width: 100%;
}

.feedback {
  padding: 50px 0 90px;
