  }
}
.feedback-title {
  margin-bottom: 37px;
}
@media (max-width: 767.98px) {
  .feedback-title {
    text-align: center;
  }
}
@media (max-width: 540.98px) {
  .feedback-title {
    text-align: left;
  }
}
@media (max-width: 767.98px) {
  .feedback-form {
