.feedback-form__wrapper {
  margin-top: 15px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
@media (max-width: 767.98px) {
  .feedback-form__wrapper {
    justify-content: center;
  }
}
.feedback-form__label {
  position: relative;
}
@media (max-width: 767.98px) {
