  border-radius: 10px;
  font-size: 14px;
  font-weight: 400;
  line-height: 15px;
  resize: none;
}
.feedback-form__btn {
  margin-top: 37px;
  width: 133px;
  height: 50px;
}
@media (max-width: 767.98px) {
  .feedback-form__btn {
    width: 100%;
  }
}

.more-posts {
  padding: 60px 0 100px;
}
@media (max-width: 540.98px) {
  .more-posts {
    padding: 30px 0 60px;
  }
}
@media (max-width: 767.98px) {
  .more-posts__swiper {
    text-align: center;
  }
}
.more-posts__title {
  margin-bottom: 32px;
}
@media (max-width: 767.98px) {
  .more-posts__title {
    text-align: center;
  }
}
@media (max-width: 540.98px) {
  .more-posts__title {
